.inst {
  text-align: center;
  color: $medium_black;

  & > h2 {
    @include text(34px, 44px, 700);
    letter-spacing: 0.03em;
    margin-bottom: 24px;

    @media (max-width: $desktop-width) {
      margin-bottom: 19px;
    }
  }

  & > a {
    @include text(16px, 21px, 500);
    border-bottom: 1px solid $basic_color;
    text-decoration: none;
    color: $medium_black;
    cursor: pointer;
  }
}

.inst__list {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr;
  row-gap: 25px;
  column-gap: 27px;
  @include padding(65px, 132px);

  @media (max-width: $desktop-width) {
    row-gap: 20px;
    column-gap: 16px;
    @include padding(60px, 80px);
  }

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr 1fr;
    row-gap: 27px;
    column-gap: 24px;
    @include padding(46px, 71px);
  }
}

.inst__item {
  min-width: 230px;
  max-height: 230px;

  @media (max-width: $desktop-width) {
    min-width: 133px;
    max-height: 133px;
  }
}

.inst__item--big {
  grid-row: span 2;
  max-width: 399px;
  max-height: 412px;

  @media (max-width: $desktop-width) {
    max-width: 231px;
    max-height: 240px;
  }

  @media (max-width: $tablet-width) {
    max-width: 233px;
    max-height: 247px;
  }
}

.inst__item--small {
  max-height: 150px;

  @media (max-width: $desktop-width) {
    max-height: 87px;
  }
}

.inst__item--no-mobile {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.inst__link img {
  // min-width: 230px;
  // min-height: 150px;
  // max-width: 230px;
  // max-height: 230px;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
